<template>
  <div class>
    <div class="layout">
      <div class="loginmain">
        <div class="loginbox">
          <div class="logininner">
            <div class="logonj"></div>
            <div class="loginform">
              <div class="mtb02">
                <h1>社員コード</h1>
                <Input prefix="ios-contact" placeholder="Enter name" style="width: auto"/>
              </div>
        
              <div class="mtb20">
                <h1>パスワード</h1>
                <Input prefix="ios-key" placeholder="Password" type="password" style="width: auto"/>
              </div>
              <div class="notice">
                <Row :gutter="16">
                    <Col span="12" offset="12">
                        <div class="text-right"><a href="">パスワードを忘れる？</a></div>
                    </Col>
                </Row>
                  
              </div>
              <div class="loginbtn">
                  <Button shape="circle" type="primary">ログイン</Button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  components: {}
};
</script>


