                                      <Col span="2">
                                          <Input v-model="formItem.input" placeholder=""></Input>
                                      </Col>
                                      <Col span="2">
                                          <Input v-model="formItem.input" placeholder=""></Input>
                                      </Col>
                                      <Col span="2">
                                          <Input v-model="formItem.input" placeholder=""></Input>
                                      </Col>
                                      <Col span="2">
                                          <Input v-model="formItem.input" placeholder=""></Input>
                                      </Col>
                                    </Row>
                                  </FormItem>
                                </Form>
                              </div>
                            </Panel> 
                        </Collapse>
                      </col>
                  </Row>
                </div>
            </Modal>
            <!-- END Modal -->
          </Content>
        </Layout>
      </Layout>
    </Layout>
  </div>
</template>
<script>
export default {
  name: "WorkTimeTypeRegister",
  components: {},
  data() {
    return {
      ok:'登録',
      cancel:'キャンセル',
      modal11: false,
      value1: false,
      formTop: {
                    input1: '' 
                },
      styles: {
                    // height: 'calc(100% - 55px)',
                    overflow: 'auto',
                    paddingtop: '53px',
                    // paddingBottom: '53px',
                    position: 'static'
                },
                formData: {
                    name: '',
                    url: '',
                    owner: '',
                    type: '',
                    approver: '',
                    date: '',
                    desc: ''
                },
      formItem: {
        input: "",
        select: "",
        radio: "male",
        checkbox: [],
        switch: true,
        date: "",
        time: "",
        slider: [20, 50],
        textarea: ""
      },
      isCollapsed: false,
      columns12: [
        {
          title: "勤務体系コー",
          width: 110,
          slot: "Num1",
          key: "Num"
        },
        {
          title: "勤務体系名",
          slot: "Num2",
          key: "Name"
        },
        {
          title: "勤務形態",
          slot: "Num3",
          key: "Form"
        },
        {
          title: "所定時刻",
          slot: "Num4",
          key: "time1"
        },
        {
          title: "所定時間",
          width: 100,
          slot: "Num5",
          key: "time2"
        },
        {
          title: "休憩時間1",
          slot: "Num6",
          key: "relax1"
        },
        {
          title: "休憩時間2",
          slot: "Num7",
          key: "relax2"
        },
        {
          title: "休憩時間3",
          slot: "Num8",
          key: "relax3"
        },
        {
          title: " ",
          slot: "action",
          width: 220,
          align: "center"
        }
      ],
      data6: [
        {
          Num: "0101",
          Name: "朝8時固定",
          Form: "固定勤",
          time1: "8:00～17:00",
          time2: "8:00",
          relax1: "7:00～ 8:00",
          relax2: "12:00～13:00",
          relax3: ""
        }
      ],
      editIndex:-1,
      num:'',
    };
  },
  computed: {
    rotateIcon() {
      return ["menu-icon", this.isCollapsed ? "rotate-icon" : ""];
    },
    menuitemClasses() {
      return ["menu-item", this.isCollapsed ? "collapsed-menu" : ""];
    }
  },
  methods: {
    collapsedSider() {
      this.$refs.side1.toggleCollapse();
    },
    show(index) {
      this.$Modal.info({
        title: "下記の勤務体系を廃止してもよらしいですか？",
        content: 
        `勤務体系コー：${this.data6[index].Num}
        <br>
        勤務体系名：${this.data6[index].Name}
        <br>
        勤務形態：${this.data6[index].Form}`
      });
    },
    remove(index) {
      this.data6.splice(index, 1);
    },
    edit(index){
      this.editIndex = index
    }
  }
};
</script>



<style scoped>
.layout {
  border: 1px solid #d7dde4;
  background: #f5f7f9;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}
.layout-header-bar {
  background: #fff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}
.layout-logo-left {
  width: 90%;
  height: 30px;
  background: #5b6270;
  border-radius: 3px;
  margin: 15px auto;
}
.layout-nav {
    display: inline-block;
    margin: 0 auto;
}
.menu-icon {
  transition: all 0.3s;
}
.rotate-icon {
  transform: rotate(-90deg);
}
.menu-item span {
  display: inline-block;
  overflow: hidden;
  width: 130px;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: middle;
  text-align: left;
  transition: width 0.2s ease 0.2s;
}
.menu-item i {
  transform: translateX(0px);
  transition: font-size 0.2s ease, transform 0.2s ease;
  vertical-align: middle;
  font-size: 16px;
}
.collapsed-menu span {
  width: 0px;
  transition: width 0.2s ease;
}
.collapsed-menu i {
  transform: translateX(5px);
  transition: font-size 0.2s ease 0.2s, transform 0.2s ease 0.2s;
  vertical-align: middle;
  font-size: 22px;
}
</style>


